.perseids-react-components--navbar {
  min-height: 72px;
}

.perseids-react-components--navbar-brand {
  padding-top: 8px!important;
  padding-bottom: 8px!important;
}

.perseids-react-components--navbar-logo-img {
  height: 40px;
}

.navbar-light .navbar-nav .nav-link {
  color: rgba(0, 0, 0, 0.54);
}

.navbar-light .navbar-text {
  color: rgba(0, 0, 0, 0.54);
}

html {
  position: relative;
  min-height: 100%;
  padding-bottom: 62px;
}

.perseids-react-components--footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 62px;
}

.perseids-react-components--footer-nav {
  height: 100%;
}

.perseids-react-components--twitter {
  height: 30px;
}

.perseids-react-components--octicon {
  height: 24px;
}

